import React from "react";

type ProductCardProps = {
  title: string;
  price: number;
  image: string;
  description: string;
  discountedPrice?: number;
};

export default function ProductCard({
  title,
  price,
  image,
  description,
  discountedPrice,
}: ProductCardProps) {
  return (
    <div className="border rounded bg-white flex flex-col h-full overflow-hidden">
      <img
        src={image}
        alt={title}
        className="w-full h-64 object-contain border-b"
      />
      <div className="p-4 flex-1 flex flex-col">
        <h2 className="font-mono text-xl font-semibold mb-2">{title}</h2>
        <p className="font-mono text-gray-600 mb-4 flex-1">{description}</p>
        <div className="font-mono text-lg mt-auto">
          {discountedPrice ? (
            <>
              <span className="text-gray-400 line-through mr-2">
                ${price.toFixed(2)}
              </span>
              <span className="text-black font-bold">
                ${discountedPrice.toFixed(2)}
              </span>
            </>
          ) : (
            <span className="text-black font-bold">${price.toFixed(2)}</span>
          )}
        </div>
      </div>
    </div>
  );
}
